body {
  margin: 0;

  font-family:  /* For Mac San Francisco */ -apple-system, BlinkMacSystemFont,
    /* Windows */ 'Helvetica Neue', /* Android / Chrome OS */ 'Roboto',
    /* KDE -> Ubuntu -> GNOME */ 'Oxygen', /* Old everything else */ sans-serif;
}

.app {
  display: flex;
  width: 100vw;
  height: 100vh;
  background-color: #161616;
  color: white;

  // So the Tilt doesn't overflow off screen and show scrollbars
  overflow: hidden;
}

.app__header {
  margin-top: auto;
}

.app__tilt {
  display: flex;
  flex-direction: column;
  margin: 20px;
  width: 100%;
}

.app__footer {
  display: flex;
  margin-top: auto;
  align-items: flex-end;
}

.app__footer-links {
  margin-left: auto;
  text-align: right;
}

.app__link {
  display: block;
  color: white;
  text-decoration: none;

  &:visited {
    color: unset;
  }

  &:hover {
    color: red;
  }
}
